/* Global ========================================================================== */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Document ========================================================================== */
html {
  position: relative;
  overflow: auto;
  width: 100%;
  min-height: 100vh;
  font-size: 62.5%;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

/* Sections ========================================================================== */
body {
  position: relative;
  overflow: hidden;
  width: 100%;
  min-height: 100vh;
  margin: 0;
}

header,
menu,
nav,
main,
section,
article,
aside,
details,
figcaption,
figure,
footer,
hgroup {
  display: block;
}

main {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-weight: normal;
  text-rendering: optimizeLegibility;
}

/* Grouping content ========================================================================== */
hr {
  overflow: visible;
  box-sizing: content-box;
  height: 0;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

/* Text-level semantics ========================================================================== */
a {
  background-color: transparent;
  text-decoration: none;
}

menu,
ul {
  list-style: none;
}

ol {
  list-style-type: decimal;
  list-style-position: outside;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Embedded content ========================================================================== */
img {
  max-width: 100%;
  border-style: none;
  display: block;
  object-fit: cover;
}

/* Forms ========================================================================== */
button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  padding: 0;
  max-width: 100%;
  display: table;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

iframe,
fieldset,
legend,
img,
button,
input[type=text] {
  border: none;
}

/* Interactive ========================================================================== */
details {
  display: block;
}

summary {
  display: list-item;
}

/* Misc ========================================================================== */
template {
  display: none;
}

[hidden] {
  display: none;
}

body {
  background-color: hsl(43deg, 24%, 94%);
  background-image: url(../img/bg.png);
  background-size: cover;
  background-position: center;
  font-family: "Raleway", sans-serif;
  font-size: 1.8rem;
  font-weight: 400;
  color: hsl(199deg, 85%, 10%);
}

h1,
h2,
h3,
h4 {
  font-family: "Gideon Roman", cursive;
}

h1 {
  font-size: 9.6rem;
  font-weight: 400;
  color: hsl(199deg, 81%, 35%);
}

h2 {
  font-size: 7.2rem;
  font-weight: 400;
  color: hsl(199deg, 81%, 35%);
}

h3 {
  font-size: 3.6rem;
  font-weight: 400;
  color: hsl(199deg, 81%, 35%);
}

h4 {
  font-size: 2.4rem;
  font-weight: 400;
  color: hsl(199deg, 81%, 35%);
  margin-bottom: 1.6rem;
}

a {
  margin-block: 0.8rem;
  font-size: 1.8rem;
  font-weight: 400;
  color: hsl(212deg, 54%, 19%);
  font-family: "Raleway", sans-serif;
  transition: all 300ms ease;
}
a:hover {
  color: hsl(199deg, 81%, 35%);
  text-decoration: underline;
}

.container {
  width: 90%;
  margin: 0 auto;
}
@media (min-width: 992px) {
  .container {
    width: min(80%, 140rem);
  }
}

.font-weight-700 {
  font-weight: 700;
}

.header {
  display: flex;
  align-items: center;
  margin-block: 3.2rem;
}

.nav {
  margin-left: auto;
}
.nav__list {
  visibility: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: hsla(43deg, 24%, 94%, 0.8);
  opacity: 0;
  transition: all 300ms ease;
}
@media (min-width: 992px) {
  .nav__list {
    visibility: visible;
    flex-direction: row;
    gap: 2rem;
    width: fit-content;
    height: fit-content;
    position: relative;
    background-color: transparent;
    opacity: 1;
  }
}
@media (min-width: 1200px) {
  .nav__list {
    gap: 4rem;
  }
}
.nav__list.active {
  visibility: visible;
  opacity: 1;
}
.nav__list img {
  display: none;
}
@media (min-width: 992px) {
  .nav__list img {
    display: block;
  }
}
.nav__toggle-button {
  position: relative;
  border: none;
  border-radius: 25%;
  width: 3.5rem;
  height: 3.5rem;
  background-color: transparent;
  background-image: url("../img/header_toggle-button.png");
  background-size: auto;
  background-position: center;
  background-repeat: no-repeat;
  transition: all 300ms ease;
}
.nav__toggle-button:hover {
  border-radius: 50%;
  background-color: hsla(199deg, 81%, 35%, 0.4);
  cursor: pointer;
}
@media (min-width: 992px) {
  .nav__toggle-button {
    display: none;
  }
}

.footer {
  display: grid;
  gap: 3.5rem;
  margin-block: 3rem;
  color: hsl(199deg, 81%, 35%);
}
@media (min-width: 380px) {
  .footer {
    grid-template-columns: repeat(2, 1fr);
    margin-block: 5rem;
  }
}
@media (min-width: 576px) {
  .footer {
    grid-template-columns: repeat(3, 1fr);
    margin-block: 6rem;
  }
}
@media (min-width: 992px) {
  .footer {
    grid-template-columns: 2fr repeat(3, 1fr);
    margin-block: 7rem;
  }
}
.footer__nav {
  display: grid;
}
.footer__nav a {
  color: hsl(199deg, 81%, 35%);
}
.footer__contact p {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.footer__contact p::before {
  content: url(../img/footer_contact-icon.svg);
}
.footer__contact-container {
  display: grid;
  gap: 3rem;
}
@media (min-width: 380px) {
  .footer__contact-container {
    grid-column: 1/3;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 576px) {
  .footer__contact-container {
    grid-column: 3/4;
    grid-template-columns: 1fr;
  }
}
@media (min-width: 992px) {
  .footer__contact-container {
    grid-column: 4/5;
  }
}
.footer__schedule-container {
  display: grid;
  gap: 3rem;
}
@media (min-width: 380px) {
  .footer__schedule-container {
    grid-column: 1/3;
    text-align: center;
  }
  .footer__schedule-container picture {
    display: inline-block;
  }
}
@media (min-width: 576px) {
  .footer__schedule-container {
    grid-column: 1/4;
  }
}
@media (min-width: 992px) {
  .footer__schedule-container {
    grid-column: 1/2;
    grid-row-start: 1;
    text-align: left;
  }
}
.footer__socials {
  display: flex;
  gap: 2rem;
}